@import url(https://fonts.googleapis.com/css?family=Roboto:400,700,900&display=swap);
body,
p,
h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "Roboto", sans-serif;
}

/* General */
.box-shadow {
  box-shadow: 0 1px 6px gray;
}

/* Header */
header {
  background-color: #fff;
}

header nav {
  padding: 2.5em 8.5em;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.nav-brand {
  font-weight: bold;
  font-size: 1.5rem;
}

.nav-brand a,
.nav-link {
  color: black;
  text-decoration: none;
  cursor: pointer;
}

nav ul {
  list-style: none;
}

ul li {
  font-size: 1.2rem;
  display: inline-block;
  padding: 0 1em;
}

.cart-btn {
  cursor: pointer;
}

.items-amount {
  font-size: 0.7em;
  padding: 2px 4px;
  border-radius: 4px;
  background-color: #000;
  color: #fff;
  margin-right: 0.5em;
}

/* Footer */
footer {
  padding: 3% 6%;
  width: 100%;
  text-align: center;
  pointer-events: none;
}

/* general */
a {
  cursor: pointer;
  color: inherit;
  text-decoration: none;
}

.shop-container {
  padding: 4% 8em;
  width: 100%;
  background-color: hsl(0 0% 96%);
  display: flex;
  justify-content: space-evenly;
}

/* shop intro */

.shop-intro {
  text-align: center;
}

/**** Carousel ****/
.carousel-container {
  position: relative;
}

.carousel-slide {
  display: none;
}

.carousel-slide-title {
  position: absolute;
  left: calc(50% - 180px);
  top: 25%;
  color: #fff;
}

.carousel-slide img {
  width: 100%;
  max-height: 600px;
  object-fit: cover;
}

.prev,
.next {
  padding: 0.5em;
  border-radius: 4px;
  cursor: pointer;
  position: absolute;
  top: 45%;
  transition: 0.6s ease;
  color: #fff;
  font-size: 2rem;
}

.fa-arrow-circle-left,
.fa-arrow-circle-right {
  pointer-events: none;
}

.prev:hover,
.next:hover {
  color: rgb(27, 27, 27);
  background-color: rgba(253, 253, 253, 0.8);
}

.prev {
  left: 0;
  margin-left: 8px;
}

.next {
  right: 0;
  margin-right: 8px;
}

.fade {
  -webkit-animation-name: fade;
          animation-name: fade;
  -webkit-animation-duration: 1.5s;
          animation-duration: 1.5s;
}

@-webkit-keyframes fade {
  from {
    opacity: 0.4;
  }
  to {
    opacity: 1;
  }
}

@keyframes fade {
  from {
    opacity: 0.4;
  }
  to {
    opacity: 1;
  }
}

.active,
.dot:hover {
  background-color: rgb(209, 209, 209);
}

/* select */
select {
  padding: 0.5em 1.25em 0.5em 0.5em;
  margin-left: 0.5em;
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  border: none;
  outline: none;
  cursor: pointer;
  font-size: 0.8em;
  background-color: #fff;
}

.select {
  position: relative;
  display: flex;
  align-items: center;
}

.select::after {
  font-family: "Font Awesome 5 Free";
  font-weight: 900;
  content: "\f107";
  font-size: 12px;
  pointer-events: none;
  position: absolute;
  right: 4px;
}

/* reset */
.products-header button {
  padding: 0.5em;
  font-size: 0.75em;
  border-radius: 4px;
  background-color: #fff;
  outline: none;
  border: none;
  box-shadow: 0 0 2px gray;
}

.products-header button:hover {
  background-color: hsl(0 0% 25%);
  color: #fff;
}

/* filter */
.filter {
  padding: 4em 0;
  width: 200px;
  flex: 0 0 15%;
}

.filter-price h4 {
  position: relative;
  left: -8px;
  margin-bottom: 2.5em;
}

.input-range__track--active,
.input-range__slider {
  background: gray !important;
  border-color: gray !important;
}

/* products panel */
.products-container {
  flex: 0 0 80%;
  margin-left: 2em;
}

.products-header {
  display: flex;
  justify-content: flex-end;
  padding: 0 10px;
}

.products-header div {
  font-size: 1rem;
  font-weight: 700;
  margin: 0 1em;
}

/* products card */
.products-body {
  margin-top: 2.5em;
  display: flex;
  flex-wrap: wrap;
}

.products-card {
  position: relative;
  margin: 0 1% 8em 1%;
  flex: 0 0 23%;
  height: 300px;
  text-align: center;
  border-radius: 10px;
  background-color: #fff;
}

.add-btn {
  width: 100%;
  height: 36px;
  padding-top: 0.5em;
  position: absolute;
  bottom: -36px;
  background-color: hsl(0 0% 98%);
  opacity: 0;
  transition: all 0.5s ease-in-out;
  pointer-events: visible;
  border-radius: 10px;
}

.products-card:hover {
  transition: all 0.35s linear;
  box-shadow: 0 0 12px gray;
  position: relative;
  top: -2px;
}

.products-card:hover .add-btn {
  opacity: 1;
  bottom: 0;
  pointer-events: all;
  cursor: pointer;
}

.products-card img {
  width: 100%;
  height: 300px !important;
  object-fit: contain;
}

.products-card h4 {
  margin: 10px 0;
}

.products-card p {
  margin: 6px 0;
}

.sticky {
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 999;
}

/* loading */
.loading-container {
  position: fixed;
  z-index: 999;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(255, 255, 255, 1);
}

.loader {
  position: absolute;
  width: 50%;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: auto;
}

/* pagination btns */
.pagination-container {
  padding: 8px 12px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.previous-btn,
.next-btn,
.page-btn {
  cursor: pointer;
  padding: 4px 8px;
  background-color: #fff;
  border-radius: 4px;
}

.previous-btn:hover,
.next-btn:hover,
.page-btn:hover {
  transition: 0.3s ease;
  color: #fff;
  text-decoration: underline;
  background-color: rgb(226, 226, 226);
}

.previous-btn,
.next-btn {
  text-align: center;
  flex: 0 0 5%;
}

.page-btn-container {
  display: flex;
}

.page-btn {
  margin: 0 4px;
  overflow: hidden;
}

.active {
  border: 1px solid black;
}

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

.rounded-border {
  border-radius: 4px;
}

.shopping-cart::before,
.shopping-cart::after {
  content: "";
  position: absolute;
  bottom: 100%;
  right: 8.1em;
  width: 0;
  height: 0;
  border: solid transparent;
  pointer-events: none;
}

.shopping-cart::before {
  border-bottom-color: gray;
  border-width: 16px;
}

.shopping-cart::after {
  border-bottom-color: white;
  border-width: 13px;
  right: 8.3em;
}

.shopping-cart {
  position: absolute;
  z-index: 10;
  top: 4.8em;
  right: 1em;
  width: 450px;
  background-color: hsl(0 0% 96%);
  border-radius: 6px;
  border: 2px solid gray;
  box-shadow: 0 0 2px gray;
}

.hide-shopping-cart {
  display: none;
}

.shopping-cart-title,
.shopping-cart-footer {
  margin: 0 auto;
  width: 99.5%;
  padding: 0.5em 1.3em;
  background-color: white;
}

.shopping-cart-body {
  padding: 1em 2.5em;
  height: 450px;
  overflow: auto;
}

.shopping-cart-card {
  padding: 1em;
  margin: 1em 0;
  display: flex;
  background-color: white;
  justify-content: space-between;
}

.shopping-cart-card img {
  width: 150px;
  height: 150px;
  object-fit: cover;
}

.item-info,
.shopping-cart-card img {
  flex: 0 0 40% !important;
}

.item-info h4 {
  margin-bottom: 0.8em;
}

.item-info p {
  margin-bottom: auto;
}

.btn {
  width: 80px;
  padding: 4px 0;
  border-radius: 4px;
  text-align: center;
  margin-left: auto;
  cursor: pointer;
  color: red;
  background-color: hsl(0 0% 96%);
}

.btn:hover {
  box-shadow: 0 0 6px gray;
}

.item-info > .btn {
  margin-top: 58%;
}

/* subtotal */
.subtotal {
  padding: 0.5em 1em;
  background-color: white;
}

.subtotal h4,
.subtotal p {
  display: inline;
}

.subtotal p {
  margin-left: 1em;
}

/* shopping cart footer */
.shopping-cart-footer {
  display: flex;
}

.shopping-cart-footer .btn:nth-child(2) {
  margin-left: 1em;
}

.pop-up {
  position: fixed;
  z-index: 999;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  -webkit-animation: opacity 0.3s linear;
          animation: opacity 0.3s linear;
}

@-webkit-keyframes opacity {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes opacity {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.pop-up-window {
  padding: 3em;
  position: absolute;
  color: black;
  width: 50%;
  height: 400px;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: auto;
  border-radius: 0.3em;
  background-color: #fff;
  opacity: 1;
  vertical-align: middle;
  text-align: center;
}

.pop-up-window img {
  width: 200px;
  height: 150px;
  object-fit: contain;
}

.pop-up-window h3 {
  color: #5cb644;
  margin: 0.8em 0;
}

.pop-up-window h3::before {
  font-family: "Font Awesome 5 Free";
  font-weight: 900;
  margin-right: 0.5em;
  content: "\f058";
  font-size: 17px;
  color: #5cb644;
}

.pop-up-window button,
.view-cart-btn {
  padding: 0.65em;
  border: 0;
  outline: 0;
  font-size: 0.75rem;
  background-color: #f08222;
  border-radius: 3px;
  text-align: center;
  color: hsl(0 0% 96%);
}

.pop-up-window button:hover,
.view-cart-btn:hover {
  transition: all 0.2s ease-in;
  background-color: #d4731e;
}

.btns {
  margin: 1em auto;
  width: 240px;
  display: flex;
  justify-content: space-between;
}

.close-btn {
  position: absolute;
  top: 0.6em;
  right: 0.8em;
}

.close-btn:hover {
  -webkit-animation: rotate 0.5s linear;
          animation: rotate 0.5s linear;
}

@-webkit-keyframes rotate {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(180deg);
  }
}

@keyframes rotate {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(180deg);
  }
}

.product-name {
  font-weight: 700;
}

.fa-times {
  font-size: 1.5rem;
  color: gray;
}

.fa-times:hover {
  color: #000;
}

.detail-container {
  padding: 6% 8em;
  display: flex;
}

.detail-container-body {
  flex: 0 0 76%;
  display: flex;
  align-items: flex-start;
  padding: 0 0.5em;
  flex-direction: column;
}

.detail-container-body h1 {
  width: 80%;
  color: #0d4f77;
  margin-bottom: 1.5em;
}

.detail-img-specs {
  width: 100%;
  display: flex;
  align-items: center;
}

.imgs-container {
  flex: 0 0 35%;
}

.detail-img:first-child {
  display: block;
}

.detail-img {
  display: none;
}

.detail-img > img {
  width: 550px;
  height: 550px;
  object-fit: cover;
}

.thumbnail-container {
  margin-top: 4px;
  display: flex;
  cursor: pointer;
}

.thumbnail-img {
  transition: opacity 0.2s ease-out;
  border: 1px solid hsl(0, 0%, 90%);
  margin-right: 2px;
  opacity: 0.5;
}

.thumbnail-img:hover,
.thumbnail-active {
  transition: all 0.2s ease-in;
  opacity: 1;
}

.thumbnail-img > img {
  width: 80px;
  height: 80px;
  pointer-events: none;
}

.detail-specs {
  flex: 0 0 55%;
  padding: 2em 0.2em;
  margin-left: 1em;
  border: 2px solid #f6f7fa;
  border-radius: 4px;
}

.detail-specs-overall-info {
  width: 90%;
  margin: 0 auto;
  padding: 0.5em 0.6em;
  background-color: hsl(0, 0%, 96%);
  font-size: 0.9rem;
  border-radius: 4px;
  margin-bottom: 1em;
}

.detail-specs-overall-info b {
  color: hsl(0, 0%, 70%);
}

.detail-specs ul li {
  width: 90%;
  padding-left: 0;
  padding-bottom: 0.4em;
  font-size: 0.9rem;
  display: block;
  margin: 0.6em auto;
  border-bottom: 1px solid hsl(0, 0%, 90%);
}

.detail-specs ul li b {
  display: inline-block;
  width: 50%;
}

/* add to cart */
.add-to-cart {
  padding: 1em;
  flex: 0 0 24%;
  text-align: center;
  border: 2px solid #f6f7fa;
  border-radius: 4px;
  height: 250px;
}

.add-to-cart h2 {
  padding-bottom: 0.25em;
  border-bottom: 1.5px solid hsl(0, 0%, 70%);
}

.add-to-cart p {
  margin: 1.25em 0;
  text-align: start;
  font-size: 0.85rem;
}

.pick-up::before,
.delivery::before {
  font-family: "Font Awesome 5 Free";
  font-weight: 900;
  margin: 0.5em;
}

.pick-up::before {
  content: "\f057";
  color: #ee3924;
}

.delivery::before {
  content: "\f058";
  color: #5cb644;
}

.fa-times-circle {
  color: #ee3924;
  font-weight: 900;
  margin-right: 0.5em;
}

.fa-shopping-cart {
  margin-right: 0.5em;
  color: hsl(0, 20%, 92%);
}

.add-to-cart-btn {
  border: none;
  outline: none;
  border-radius: 2px;
  color: #fff;
  font-size: 1rem;
  font-weight: 700;
  background-color: #f08222;
  padding: 0.5em 2em;
  width: 98%;
  cursor: pointer;
}

.add-to-cart-btn:hover {
  transition: all 0.2s ease-in;
  background-color: #d4731e;
}

.container h1 {
  padding-bottom: 0.3em;
  margin-bottom: 1em;
  color: #0d4f77;
  text-transform: uppercase;
  border-bottom: 2px solid hsl(0 0% 95%);
}

.container {
  padding: 4% 8em;
}

/* empty cart */
.empty-cart {
  padding: 2em;
  text-align: center;
  border: 1px solid hsl(0 0% 90%);
}

.empty-cart .fa-shopping-cart {
  font-weight: 900;
  color: #0d4f77;
  font-size: 45px;
}

.empty-cart h1 {
  border: none;
  margin: 0;
  text-transform: none;
}

table {
  width: 100%;
  margin-top: 2em;
  border-collapse: collapse;
}

.table-body tr td {
  border-top: 1px solid hsl(0 0% 90%);
}

.table-body td button {
  border: none;
  outline: none;
  cursor: pointer;
  font-size: 1rem;
}

button .fa-times-circle {
  color: #e6e3e3;
}

button .fa-times-circle:hover {
  transition: all 0.2s ease-in;
  color: #ee3924;
}

th {
  background-color: hsl(0 0% 95%);
}

th,
td {
  padding: 8px 10px;
  text-align: start;
}

td img {
  width: 100px;
  height: 100px;
}

.item-name {
  vertical-align: middle;
}

/* clear section */
tbody:nth-last-child(1) {
  background-color: hsl(0 0% 94%);
}

.clear-section {
  border: none;
  outline: none;
  padding: 6px 8px;
  border-radius: 2px;
  background-color: #51b2ee;
  color: #fff;
  cursor: pointer;
  font-size: 0.85rem;
}

.clear-section:hover {
  transition: all 0.2s ease-in;
  background-color: #2582bb;
}

/* my cart subtotal */
.my-cart-subtotal {
  padding: 1em;
  width: 300px;
  margin-top: 36px;
  margin-left: auto;
  border: 1px solid hsl(0 0% 90%);
  text-align: right;
}

.subtotal-top,
.subtotal-mid {
  padding-bottom: 12px;
}

.subtotal-mid {
  border-bottom: 1px solid hsl(0 0% 90%);
}

.my-cart-subtotal .left,
.my-cart-subtotal .right {
  display: inline-block;
  width: 50%;
}

.subtotal-bot {
  margin-top: 12px;
}

.subtotal-bot button {
  border: none;
  outline: none;
  cursor: pointer;
  margin-top: 12px;
  padding: 8px 10px;
  font-size: 0.85rem;
  border-radius: 2px;
  background-color: #2cc732;
  color: #fff;
}

.unit {
  font-size: 0.8rem;
  font-weight: 900;
}

.subtotal-bot button:hover {
  transition: all 0.2s ease-in;
  background-color: #24ac28;
}

.fa-shield-alt {
  margin-right: 6px;
}

.fa-chevron-right {
  margin-left: 6px;
}

/* Paid info */
.paid-info {
  padding: 2em;
  text-align: center;
  border: 1px solid hsl(0 0% 90%);
}

.paid-info h1 {
  border: none;
  color: #1ed124;
  margin-bottom: 0;
  text-transform: none;
}

.paid-info .fa-shopping-cart {
  font-weight: 900;
  color: #1ed124;
  font-size: 45px;
}

.black-container {
  position: fixed;
  z-index: 999;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
}

.black-container h1 {
  text-transform: none;
  border-bottom: 1px solid hsl(0, 0%, 96%);
  margin-bottom: 0;
  margin-top: 4px;
}

.alert-section {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  margin: auto;
  width: 600px;
  height: 240px;
  border-radius: 4px;
  text-align: center;
  background-color: #fff;
}

.alert-content {
  padding: 3.5em 3.5em 3.5em 3em;
  text-align: right;
}

.alert-section .btns {
  margin: 15px 0;
  width: auto;
  display: flex;
  justify-content: flex-end;
}

.alert-section .btns button {
  border: none;
  outline: none;
  cursor: pointer;
  font-size: 0.7rem;
  padding: 0.8em 2.5em;
  margin: 0 4px;
  color: #fff;
  border-radius: 4px;
}

.alert-section .btns button:nth-child(1) {
  background-color: #f08222;
}
.alert-section .btns button:nth-child(2) {
  background-color: #999;
}

.alert-section .btns button:nth-child(1):hover {
  transition: all 0.2s ease-in;
  background-color: #da7722;
}

.alert-section .btns button:nth-child(2):hover {
  transition: all 0.2s ease-in;
  background-color: rgb(134, 134, 134);
}

